const Base = require('../base.js');
//获取用户列表
module.exports = class extends Base {
    async indexAction() {
        let model = this.model('user_info');
        let data = ""

        data = await model.page(this.ctx.post('page')).order('id DESC').where({
            user_name: ['like', `%${this.ctx.post('user_name')||""}%`]
        }).countSelect();


        return this.fail(data); //2输出json 推荐

    }

};
